.header {
  position: relative;
  z-index: 999999;
  padding: 24px 72px;
  color: var(--color-text-accent);

  @include media-breakpoint-down(xxl) {
    padding: 0;
  }
  &__logo {
    display: flex;
    text-decoration: none;
    position: relative;
    z-index: 999;
    width: 194px;
    height: 80px;
    @include media-breakpoint-down(xl) {
      width: 140px;
    }
    img {
      transition: all 0.3s;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
    @include media-breakpoint-down(xl) {
      margin-right: auto;
      transition: all 0.3s;
    }
    &:hover {
      img {
        transform: scale(1.05);
      }
    }
  }

  .navbar-expand-lg {
    @include media-breakpoint-only(lg) {
      flex-wrap: wrap;
    }
    .navbar-collapse {
      @include media-breakpoint-down(xl) {
        background-color: var(--color-light);
        position: absolute;
        right: -16px;
        z-index: 99;
        width: calc(100% + 32px);
        padding: 2rem 1rem;
        opacity: 0;
        visibility: hidden;
        -webkit-box-shadow: -1px 0px 34px -1px rgba(0, 0, 0, 0.11);
        -moz-box-shadow: -1px 0px 34px -1px rgba(0, 0, 0, 0.11);
        box-shadow: -1px 0px 34px -1px rgba(0, 0, 0, 0.11);
        transition: all 0.3s;
        flex-wrap: wrap;
        top: 0;
        min-height: 100vh;
      }
      @include media-breakpoint-down(sm) {
        width: 100% !important;
        right: 0 !important;
        box-shadow: none !important;
      }
    }
  }
}
.collapse:not(.show) {
  display: flex;
}
.navbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 72px;
  align-items: center;
  @include media-breakpoint-down(xl) {
    justify-content: flex-end;
  }
  @include media-breakpoint-down(xl) {
    padding-top: 8px;
    padding-bottom: 40px;
  }
  &-nav {
    display: flex;
    @include media-breakpoint-down(xl) {
      flex-direction: column !important;
      text-align: center;
      width: 100%;
    }
    @include media-breakpoint-down(md) {
      padding-top: 48px;
    }
  }
  &-collapse {
    display: flex;
    justify-content: center;
  }
}
.is-nav-open .header .navbar-expand-lg .navbar-collapse {
  @include media-breakpoint-down(xl) {
    opacity: 1;
    visibility: visible;
    transition-delay: 0s;
  }
}
.c-header__opener {
  padding: 0.25rem 0.75rem;
  font-size: 1.25rem;
  line-height: 1;
  background-color: transparent;
  border: 0.0625rem solid transparent;
  border-radius: 0.25rem;
  transition: box-shadow 0.15s ease-in-out;
  display: block;
  position: relative;
  cursor: pointer;
  z-index: 99999;
  height: 46px;
  padding-right: 0;

  span {
    width: 24px;
    height: 3px;
    background: var(--color-text-accent);
    transform: translateY(-1px);
    box-shadow: 0 2px 5px rgba(255, 103, 47, 0.2);
    display: block;
    transition: all 0.3s;
    &::before,
    &::after {
      content: '';
      transition: all 0.3s;
      position: absolute;
      width: 24px;
      height: 3px;
      background: var(--color-text-accent);
      left: 0;
      right: 0;
      box-shadow: 0 2px 5px rgba(255, 103, 47, 0.2);
    }
    &::before {
      transform: translateY(-7px);
    }
    &::after {
      transform: translateY(7px);
    }
  }
  &:focus {
    box-shadow: none;
  }

  @include media-breakpoint-only(xl) {
    display: none;
  }
  @include media-breakpoint-only(xxl) {
    display: none;
  }
}
.c-header__opener.is-open {
  span {
    border-color: var(--color-light);
    background: var(--color-light);
  }
  span:before {
    transform: rotate(45deg);
  }
  span:after {
    transform: rotate(-45deg);
  }
}
.is-nav-open .header__logo {
  width: 120px;
  height: 60px;
}
.nav-link {
  color: var(--color-text-accent);
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  padding-left: 16px;
  padding-right: 16px;
  @include media-breakpoint-down(xl) {
    font-size: 18px;
    line-height: normal;
  }
  &.active {
    color: var(--color-text-accent-pressed) !important;
    text-decoration: underline;
    text-decoration-color: var(--color-text-accent-pressed);
  }
  &.show {
    color: var(--color-text-accent-pressed) !important;
  }
}
.nav-item {
  @include media-breakpoint-down(xl) {
    padding: 8px 0;
  }
  .nav-link:hover {
    color: var(--color-text-accent-pressed) !important;
  }
}

